.Profile-container{
    width: 50%;
    margin: 10px auto;
}

.Profile-avatar{
    width: 150px;
}

.Profile-container > ul{
    list-style: none;
    padding: 0;
    text-align: left;
}
.Profile-container > ul > li{
    display: flex;
    justify-content: space-between;
}

.Profile-container > ul >li > span{
    font-weight: 600;
}